* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Work Sans, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

header {
  height: 20vh;
  padding-top: 15px;
  padding-bottom: 15px;
}

main {
  padding-top: 30px;
  padding-bottom: 30px;
}

.container {
  width: 100%;
