  padding-right: 15px;
}

footer {
  color: #000;
  text-align: center;
  background-color: #7280ed;
  padding-top: 15px;
  padding-bottom: 20px;
  font-size: .9rem;
  font-weight: 700;
}

footer ul li {
  padding-bottom: 15px;
  padding-left: 1rem;
  display: inline-block;
}

footer a {
  color: #000;
}

footer a:hover {
  color: #000;
  text-decoration: underline;
}

footer img {
  width: 20px;
  height: 20px;
  display: inline;
}

footer p {
  padding-bottom: 0;
}

nav {
  text-align: right;
  color: #000;
}

nav ul {
  padding-left: 0;
}

nav ul li {
  padding-left: 0;
  font-size: 1.1rem;
  font-weight: 600;
  display: inline-block;
}

nav ul li:before {
  content: " · ";
  width: 10px;
}

nav ul li:first-child:before {
  content: "";
}

nav ul li a {
  color: inherit;
  border: none;
}

nav ul li a:hover {
  border: none;
}

a {
  color: #7280ed;
  text-decoration: none;
}

.logo {
  width: 48px;
  height: 60px;
}

.home {
  text-align: center;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  background-image: url("gallo-azul.1e82a7a9.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.home main {
  height: 65vh;
  padding-top: 10vh;
}

.home footer {
  background-color: #0000;
}

.home h1 {
  font-size: 3rem;
  line-height: 1;
}

.home a {
  color: inherit;
  border: none;
}

.home a:hover {
  border: none;
  text-decoration: underline;
}

.home nav ul {
