  font-weight: 600;
}

p {
  padding-bottom: 10px;
}

.tit {
  text-align: right;
  letter-spacing: -4px;
  font-family: Work Sans, sans-serif;
  font-size: 1.8rem;
  font-weight: 800;
}

.tit span {
  color: #000;
  letter-spacing: normal;
}

nav li.active {
  color: #6374fc;
  font-size: 1.4rem;
}

blockquote {
  text-align: center;
  background-color: #eeecfd;
  margin-bottom: 15px;
  padding-bottom: 10px;
  font-style: italic;
  font-weight: 500;
}

