  margin-left: 5%;
  list-style-type: circle;
}

.biblio ul {
  padding-left: 0;
  font-size: 1rem;
  list-style-image: none;
}

object {
  width: 100%;
  height: 400px;
}

@media (min-width: 768px) {
  .container {
    max-width: 80%;
  }

