@media screen and (min-width: 992px) {
  .home h1 {
    font-size: 4rem;
  }

  .home h2 {
    font-size: 1.4rem;
  }

  .container {
    width: 80%;
  }

  iframe {
    width: 60%;
    height: 400px;
  }

  figcaption {
    text-align: center;
